import React from 'react'

import { Wrapper, Nav, NavLeft, NavRight } from './styled'
import urls from '../../constants/urls'

const Header = ({ isAuthenticated }) => (
  <Wrapper>
    <Nav>
      <NavLeft>
        <Link to={urls.index}>{'Product List'}</Link>
      </NavLeft>
      <NavRight>
        {isAuthenticated ? (
          <>
            <Link href={urls.myAccount}>My Account</Link>
            <Link href={urls.logout}>Logout</Link>
          </>
        ) : (
          <>
            <Link href={urls.signIn}>Sign In</Link>
            <Link href={urls.signUp}>Sign Up</Link>
          </>
        )}
        <Link to={urls.cart}>{'My Cart'}</Link>
      </NavRight>
    </Nav>
  </Wrapper>
)

export default Header
